.story{
    position: relative;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    width:100px;
    height:100px;
    margin-right: 10px;
    transition: transform;
    cursor: pointer;
}

.story:hover{
    transform: scale(1.07);
}

.story__avatar {
    margin:10px;
    border: 5px solid #2e81f4;
}

.storyReel{
    display:flex;
}